.container--list {
  margin-top: -110px;
}

.container--list--item {
  width: 200px;
  display: inline-block;
  margin-right: 5px;
}

.list--item--img {
  width: inherit;
  transform: scale(0.9);
  transition: all ease 0.2s;
  cursor: pointer;
}
.list--item--img:hover {
  transform: scale(1);
}

@media screen and (max-width: 760px) {
  .container--list {
    margin-top: -180px;
    text-align: center;
  }
  .container--list--item {
    width: 170px;
    margin-right: 0;
  }
}