@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  font-family: Roboto, Arial;
  background-color: #222;
  color: white;
  overflow-x: hidden;
}

.App {
  text-align: center;
}

footer {
  margin: 30px;
}

.footer--title {
  color: rgb(0, 110, 255);
  font-weight: bold;
  font-size: 2rem;
}

.footer--links {
  margin: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
footer a {
  margin: 0 30px;
  color: #222;
  background-color: #ccc;
  font-weight: bold;
  text-decoration: none;
  padding: 15px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all ease 0.2s;
}
footer a:hover {
  background-color: white;
}

.footer--direitos {
  text-align: right;
}

@media screen and (max-width: 760px) {
  .footer--links {
    flex-direction: column;
  }
  footer a {
    margin: 10px;
    width: 100%;
  }
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  height: 60px;
  background-color: rgba(0, 0, 0, 0.2);
  transition: all ease 0.2s;
}

.header--black {
  background-color: rgba(0, 0, 0, 1);
}

.header--menu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header--logo {
  font-size: 1.7em;
  font-weight: 600;
  color: rgb(0, 110, 255);
  text-shadow: 1px 1px 1px rgb(158, 158, 158);
}

.header--menu a,
.header--menu div {
  margin-left: 30px;
  cursor: pointer;
  transition: all ease 0.2s;
  font-size: 1.2em;
  color: white;
  text-decoration: none;
}
.header--menu a:hover,
.header--menu div:hover {
  color: rgb(74, 150, 250);
  font-weight: bold;
}

.header--menu div{
  font-size: 1.4em;
}

@media screen and (max-width: 760px) {
  .header--logo{
    font-size: 1.2em;
  }

  .header--menu a,
  .header--menu div {
    margin-left: 12px;
    font-size: 0.9em;
  }
}
.container--featured {
  width: 100vw;
  height: 100vh;
  font-family: Roboto;
  font-size: 1.3em;
}

.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #222 10%, transparent 90%);
}
.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #222 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-top: 70px;
  padding-left: 30px;
  padding-bottom: 150px;
}

.featured--title {
  font-size: 2em;
  font-weight: bold;
}

.featured--ratings {
  margin-top: 15px;
  display: flex;
}

.featured--description,
.featured--tech {
  font-size: 1.1em;
  margin-top: 30px;
  max-width: 50%;
  text-align: left;
  color: #aaa;
}

.featured--tech {
  font-size: 1em;
  color: #ccc;
}

.featured--buttons {
  margin-top: 20px;
}

.linkedin,
.repo,
.page {
  margin-right: 20px;
  text-decoration: none;
  color: #111;
  font-weight: bold;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  padding: 10px;
  background-color: #ccc;
  border-radius: 8px;
  box-shadow: black 2px 2px 5px;
  transition: all ease 0.2s;
}
.linkedin:hover,
.repo:hover,
.page:hover {
  background-color: white;
}

.linkedin {
  color: rgb(0, 110, 255);
}
.page {
  color: purple;
}

@media screen and (max-width: 760px) {
  .container--featured {
    font-size: 1em;
  }

  .featured--horizontal {
    padding-top: 0px;
    padding-left: 10px;
    background-color: rgb(0,0,0,0.5);
  }

  .featured--title {
    font-size: 1.5em;
  }
  .featured--ratings {
    margin-top: 10px;
    font-size: 15px;
  }

  .featured--description,
  .featured--tech {
    font-size: 1em;
    max-width: 100%;
  }
  .featured--tech {
    font-size: 0.8em;
  }
  .featured--buttons {
    margin-top: 50px; 
  }
  .linkedin,
  .repo,
  .page {
    margin-right: 5px;
    font-size: 0.8em;
    padding: 2px;
    background-color: aquamarine;
  }
}
.container--list {
  margin-top: -110px;
}

.container--list--item {
  width: 200px;
  display: inline-block;
  margin-right: 5px;
}

.list--item--img {
  width: inherit;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  transition: all ease 0.2s;
  cursor: pointer;
}
.list--item--img:hover {
  -webkit-transform: scale(1);
          transform: scale(1);
}

@media screen and (max-width: 760px) {
  .container--list {
    margin-top: -180px;
    text-align: center;
  }
  .container--list--item {
    width: 170px;
    margin-right: 0;
  }
}
.modal--int {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: black;
  width: 100vw;
  height: 100vh;
  z-index: 999;
  flex-direction: column;
  justify-content: center;
}

iframe {
  align-self: center;
  width: 728px;
  height: 409px;
}

.modal--int--close {
  padding: 5px;
  width: 40px;
  margin-right: 30px;
  font-size: 2.5em;
  text-align: right;
  cursor: pointer;
  align-self: flex-end;
}

.modal--int--goals {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
}

.modal--int--container {
  width: 300px;
  text-align: justify;
}
.modal--int--title {
  color: rgb(74, 150, 250);
  font-weight: bold;
  text-align: center;
  margin-bottom: 5px;
}

@media screen and (max-width: 760px) {
  iframe {
    align-self: center;
    width: 350px;
  }

  .modal--int--close {
    margin-right: 10px;
  }

  .modal--int--goals {
    flex-direction: column;
    margin-top: -10px;
    z-index: 9;
  }

  .modal--int--container {
    width: 100%;
    padding: 10px;
  }
}
.modal--port {
