  height: inherit;
  background: linear-gradient(to right, black 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-top: 70px;
  padding-left: 30px;
  padding-bottom: 150px;
}

.modal--port--title {
  font-size: 2em;
  font-weight: bold;
}

.modal--port--ratings {
  margin-top: 15px;
  display: flex;
}

.modal--port--description,
.modal--port--tech {
  font-size: 1.1em;
  margin-top: 30px;
  max-width: 50%;
  text-align: left;
  color: #aaa;
}

.modal--port--tech {
  font-size: 1em;
  color: #ccc;
}

.modal--port--buttons {
  margin-top: 20px;
}

.linkedin,
.repo,
.page {
  margin-right: 20px;
  text-decoration: none;
  color: #111;
  font-weight: bold;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  padding: 10px;
  background-color: #ccc;
  border-radius: 8px;
  box-shadow: black 2px 2px 5px;
  transition: all ease 0.2s;
}
.linkedin:hover,
.repo:hover,
.page:hover {
  background-color: white;
}

.linkedin {
  color: rgb(0, 110, 255);
}
.page {
  color: purple;
}

@media screen and (max-width: 760px) {
  .modal--port--close {
    margin-right: 10px;
  }

  .container--port--modal {
    font-size: 1em;
  }

  .modal--port--horizontal {
    padding-top: 30px;
    padding-left: 15px;
    padding-right: 15px;
    background-color: rgb(0,0,0,0.5);
  }

  .modal--port--ratings {
    margin-top: 10px;
  }

  .modal--port--description,
  .modal--port--tech {
    max-width: 100%;
    text-align: justify;
  }

  .modal--port--tech {
    font-size: 0.9em;
  }

  .modal--port--buttons {
    margin-top: 40px;
  }
}
