.modal--port {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: black;
  width: 100vw;
  height: 100vh;
  z-index: 999;
  flex-direction: column;
  justify-content: center;
}

.modal--port--close {
  padding: 5px;
  width: 40px;
  margin-top: 50px;
  margin-right: 30px;
  font-size: 2.5em;
  text-align: right;
  cursor: pointer;
  align-self: flex-end;
}

.container--port--modal {
  width: 100vw;
  height: 100vh;
  font-family: Roboto;
  font-size: 1.3em;
}

.container--port--modal img {
  width: 100%;
  height: 100%;
}

.modal--port--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, black 10%, transparent 90%);
}
.modal--port--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, black 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-top: 70px;
  padding-left: 30px;
  padding-bottom: 150px;
}

.modal--port--title {
  font-size: 2em;
  font-weight: bold;
}

.modal--port--ratings {
  margin-top: 15px;
  display: flex;
}

.modal--port--description,
.modal--port--tech {
  font-size: 1.1em;
  margin-top: 30px;
  max-width: 50%;
  text-align: left;
  color: #aaa;
}

.modal--port--tech {
  font-size: 1em;
  color: #ccc;
}

.modal--port--buttons {
  margin-top: 20px;
}

.linkedin,
.repo,
.page {
  margin-right: 20px;
  text-decoration: none;
  color: #111;
  font-weight: bold;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  padding: 10px;
  background-color: #ccc;
  border-radius: 8px;
  box-shadow: black 2px 2px 5px;
  transition: all ease 0.2s;
}
.linkedin:hover,
.repo:hover,
.page:hover {
  background-color: white;
}

.linkedin {
  color: rgb(0, 110, 255);
}
.page {
  color: purple;
}

@media screen and (max-width: 760px) {
  .modal--port--close {
    margin-right: 10px;
  }

  .container--port--modal {
    font-size: 1em;
  }

  .modal--port--horizontal {
    padding-top: 30px;
    padding-left: 15px;
    padding-right: 15px;
    background-color: rgb(0,0,0,0.5);
  }

  .modal--port--ratings {
    margin-top: 10px;
  }

  .modal--port--description,
  .modal--port--tech {
    max-width: 100%;
    text-align: justify;
  }

  .modal--port--tech {
    font-size: 0.9em;
  }

  .modal--port--buttons {
    margin-top: 40px;
  }
}